			text-transform: unset;
		    padding: 3px 11px;
		    margin: 0 5px;
		    border: 1px solid #a1a7ac;
		}
		span.tb.active{
			color: #00aba4;
			border: 1px solid #00aba4;
		}
	}
	.tabs-sort{
		display: inline-block;
		margin-right: 104px;
		.ui.dropdown:not(.button)>.default.text{
			color: #212121;
		}
		.ui.selection.dropdown{
			padding-top: 7px;
			padding-bottom: 6px;
			min-height: auto;
			line-height: 10px;
			border: 1px solid #a1a7ac;
			background: #f6f6f6;
			border-radius: 0;
		}
		.ui.dropdown>.dropdown.icon:before{
			content: '';
			background: url('../img/arrow.png');
