		}
		span:first-child{
			margin-right: 5px;
			font-weight: 700;
		}
		span.rub{
			display: inline-block;
			margin: 0 10px;
		}
		span.rub::after{
			content: "â‚½";
			font-family: "ALSRubl";
			font-weight: 600;
			display: inline-block;
			position: relative;
			right: 15px;
		}
		span:last-child{
			margin-right: 0;
		}
	}
}
